import Snap from 'snapsvg'

Snap.plugin(function (Snap, Element) {
  Element.prototype.show = function () {
    this.attr('display', '')
  }
  Element.prototype.hide = function () {
    this.attr('display', 'none')
  }
})

// SketchPad  snap  paper
export default class Artboard {
  constructor({ width, height, imageSrc, boundingClientWidth, boundingClientHeight }) {
    this.currentElement = null
    this.elements = []
    this.redos = []
    this.undos = []

    if (imageSrc) {
      this.paper = Snap(boundingClientWidth, boundingClientHeight)
      this.boundingClientWidth = boundingClientWidth
      this.boundingClientHeight = boundingClientHeight
      this.loadImageAsBase(imageSrc)
    } else {
      this.paper = Snap(width, height)
    }
    this.insertElementType = ''
    this.insertMode = false
    this._pathd = ''
    this.setupHandler()
    this.paper.hide()
    this.setPathStyle()
    this.rotateCount = 0
  }

  setPathStyle() {
    const defaultStyle = {
      fill: 'none',
      strokeWidth: 5,
      strokeOpacity: 0.7,
      stroke: '#F9CE63',
      strokeLinecap: 'round',
      strokeLinejoin: 'round',
    }
    const localVal = localStorage.getItem(`${document.cookie}-pathStyle`)
    const localStyle = localVal && JSON.parse(localVal) || {}
    this.pathStyle = Object.assign(defaultStyle, localStyle)
  }

  hide() {
    this.paper.hide()
  }

  show() {
    this.paper.show()
  }

  clear() {
    for (const element of this.elements) element.remove()
    this.currentElement = null
    this.elements = []
    this.redos = []
    this.undos = []
  }
  rotateImage() {
    this.rotateCount += 1
    const width = this.baseImage.attr('width')
    const height = this.baseImage.attr('height')
    const scale = this.rotateScale
    let scaledWidth = width
    let scaledHeight = height
    const deg = (this.rotateCount % 4 * 90)

    const matrix = new Snap.Matrix()
    matrix.scale(scale, scale, 0, 0)
    matrix.rotate(deg, 0, 0)
    if (this.rotateCount % 4 === 2) matrix.translate(-width, -height)
    if (this.rotateCount % 4 === 1) matrix.translate(0, -height)
    if (this.rotateCount % 4 === 3) matrix.translate(-width, 0)

    if (this.rotateCount % 2 === 1) {
      scaledWidth = height * scale
      scaledHeight = width * scale
    }
    this.baseImage.transform(matrix.toTransformString())
    this.resize(scaledWidth, scaledHeight)
  }

  get rotateScale() {
    if (!this.evenRotateRatio || this.rotateCount % 2 === 0) {
      return 1
    }
    return this.oddRotateRatio / this.evenRotateRatio
  }

  getSvgtoPngDataURL(callback) {
    let paperData = this.paper.toString()
    const regex = /<image.*?<\/image>/ig
    paperData = paperData.replace(regex, '')

    const DOMURL = window.URL || window.webkitURL || window
    const svg = new Blob([paperData], { type: 'image/svg+xml' })
    const url = DOMURL.createObjectURL(svg)

    const canvas = document.createElement('canvas')
    canvas.width = this.imageBaseWidth
    canvas.height = this.imageBaseHeight
    const ctx = canvas.getContext('2d')
    const img = new Image()
    img.onload = () => {
      if (this.rotateCount % 2 === 0) {
        ctx.drawImage(img, 0, 0, this.imageBaseWidth, this.imageBaseHeight)
      } else {
        ctx.drawImage(img, 0, 0, this.imageBaseHeight, this.imageBaseWidth)
      }
      if (callback) {
        callback(canvas.toDataURL('image/png'))
      }
    }
    img.src = url
  }

  loadImageAsBase(src) {
    this.imageBaseSrc = src
    const img = new Image()
    img.onload = () => {
      const height = img.height
      const width = img.width
      this.imageBaseWidth = width
      this.imageBaseHeight = height
      this.imageBase = img
      this.evenRotateRatio = Math.min(this.boundingClientWidth / width, this.boundingClientHeight / height)
      this.oddRotateRatio = Math.min(this.boundingClientWidth / height, this.boundingClientHeight / width)
      const loadWidth = width * this.evenRotateRatio
      const loadHeight = height * this.evenRotateRatio
      if (this.baseImage) {
        this.baseImage.remove()
      }
      this.resize(loadWidth, loadHeight)
      this.baseImage = this.paper.image(src, 0, 0, loadWidth, loadHeight)
      this.clear()
      this.rotateCount = 0
    }
    img.src = src
  }

  resize(width, height) {
    this.paper.attr({ width, height })
  }

  updatePathStyle(options) {
    this.pathStyle = Object.assign(this.pathStyle, options)
  }

  setupHandler() {
    this.mouseMoveListener = e => this._mouseMove(e)
    this.mouseUpListener = () => this._mouseUp()
    this.mouseDownListener = e => this._mouseDown(e)
    this.mouseLeaveListener = () => this._mouseLeave()
  }

  appendTo(node) {
    this.paper.appendTo(node)
  }

  clearDrawSetting() {
    this.currentElement = null
    this._pathd = ''
  }

  saveNewOperation() {
    //  oeration
    this.elements.push(this.currentElement)
    this.undos.push(this.currentElement)
    this.redos = []
    this.currentElement = null
  }

  get canRedo() {
    return this.redos.length > 0
  }

  get canUndo() {
    return this.undos.length > 0
  }

  get canClear() {
    return this.elements.length > 0
  }

  get beenEdited() {
    return this.elements.length > 0
  }

  undo() {
    if (!this.canUndo) return
    const element = this.undos.pop()
    const detachedElement = element.remove()
    this.redos.push(detachedElement)
  }

  redo() {
    if (!this.canRedo) return
    const element = this.redos.pop()
    const attachedElement = element.appendTo(this.paper)
    this.undos.push(attachedElement)
  }

  _cursorMousePosition(event) {
    const bounds = this.paper.node.getBoundingClientRect()
    const x = event.clientX - bounds.left
    const y = event.clientY - bounds.top
    return {x, y, pairStr: `${x} ${y}`}
  }

  _mouseDown(event) {
    this.insertMode = true
    this.lastPos = this._cursorMousePosition(event)
    this._pathd = `M${this.lastPos.pairStr}`
    this.paper.mousemove(this.mouseMoveListener)
  }

  stopStoke() {
    this.insertMode = false
    this.paper.unmousemove(this.mouseMoveListener)
    if (this.currentElement) {
      this.saveNewOperation()
    }

    //  clear 
    this.clearDrawSetting()
  }

  _mouseUp() {
    this.stopStoke()
  }

  _mouseLeave() {
    this.stopStoke()
  }

  _mouseMove(event) {
    if (!this.insertMode) return
    const pos = this._cursorMousePosition(event)
    const dx = pos.x - this.lastPos.x
    const dy = pos.y - this.lastPos.y
    const d2 = dx * dx + dy * dy
    if (d2 <= 9) return
    this._pathd += (`L${pos.x},${pos.y}`)
    this.lastPos = pos
    if (!this.currentElement) {
      this.currentElement = this.paper.path()
      this.currentElement.attr(this.pathStyle)
    }
    this.currentElement.attr({ d: this._pathd })
  }

  activate() {
    this.paper.mousedown(this.mouseDownListener)
    this.paper.mouseup(this.mouseUpListener)
    this.paper.node.addEventListener('mouseleave', this.mouseLeaveListener)
  }

  deactivate() {
    this.paper.unmousedown(this.mouseDownListener)
    this.paper.unmouseup(this.mouseUpListener)
    this.paper.node.removeEventListener('mouseleave', this.mouseLeaveListener)
  }
}



