<template>
  <div class="sketchpad">
    <div class="sketchpad-header">
      <div class="sketch-title">
        {{studentName}}<span class="note">{{statusLabel}}</span>
        <span id="status"></span>
      </div>
      <div class="mini-gallery">
        <img v-for="artbord of artboards" :src="artbord.imageBaseSrc" @click="artboard=artbord">
      </div>

      <div class="sketch-actions">
        <button class="pad circle button" title="" @click="openViewer()">
            <icon name="slide-show"></icon>
        </button>
        <button class="pad circle rotate button" @click="rotate" title="90" v-if="reviewMode">
          <icon name="rotate-right"></icon>
        </button>
        <div class="ui dropdown line-style-selection" title="" v-show="reviewMode">
          <div class="text init-line-style"></div><icon name="caret-down"></icon>
          <div class="menu">
            <div class="header"> </div>
            <div class="item" v-for="thickness of [1, 3, 5, 10]" @click="changePenStyle(thickness, 'red')">
              <span :class="'line-style line-thickness-' + thickness"></span>
            </div>
            <div class="divider"></div>
            <div class="header"> </div>
            <template v-for="color of ['yellow', 'pink', 'green']">
              <div class="item" v-for="thickness of [3, 5, 10, 15]" @click="changePenStyle(thickness, color, 0.7)">
                <span :class="'line-style line-opacity-50 line-thickness-' + thickness + ' line-color-' + color"></span>
              </div>
            </template>
          </div>
        </div>
        <template v-if="reviewMode">
          <button class="pad circle button" @click="undo" :class="canUndo?'':'disabled'" title="">
            <icon name="undo"></icon>
          </button>
          <button class="pad circle button" @click="redo" :class="canRedo?'':'disabled'" title="">
            <icon name="redo"></icon>
          </button>
          <button class="pad circle button" @click="clear" :class="canClear?'':'disabled'" title="">
            <icon name="refresh"></icon>
          </button>
        </template>
        <template v-else>
          <button class="link link-white" @click="startReviewMode">&gt;&gt;</button>
        </template>
        <div v-if="canClose">
          |
          <button class="pad circle button" @click="$emit('close')" title="">
            <icon name="close"></icon>
          </button>
        </div>
      </div>
    </div>
    <div class="canvas-wrapper">
      <div class="artboard-wrapper"></div>
    </div>
    <div class="sketchpad-footer">
      <div class="remark" v-if="reviewMode">
        <span class="ui input">
          <input rows="1" placeholder="" v-model="note">
        </span>
      </div>
      <div class="rating" v-if="reviewMode">
        <span>/{{fullScore}}</span>
        <span class="buttons">
        <button class="rating button" @click="score=0">
          <svg width="25px" height="25px" viewBox="0 0 400 400" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
            <g id="Group">
              <path d="M200,400 C89.54305,400 0,310.45695 0,200 C0,89.54305 89.54305,0 200,0 C310.45695,0 400,89.54305 400,200 C400,310.45695 310.45695,400 200,400 Z M200,360 C288.36556,360 360,288.36556 360,200 C360,111.63444 288.36556,40 200,40 C111.63444,40 40,111.63444 40,200 C40,288.36556 111.63444,360 200,360 Z" id="Combined-Shape" fill="#DD4B39"></path>
              <text font-size="200" font-weight="400" fill="#FFFFFF">
                <tspan x="100" y="272"></tspan>
              </text>
            </g>
          </svg>
        </button>
        <button class="rating button" @click="score=fullScore*.5">
          <svg width="25px" height="25px" viewBox="0 0 400 400" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
            <defs>
              <linearGradient x1="46.8424653%" y1="49.5130139%" x2="46.7984674%" y2="49.5774137%" id="linearGradient-1">
                <stop stop-color="#00B5AD" offset="0%"></stop>
                <stop stop-color="#DD4B39" offset="100%"></stop>
              </linearGradient>
            </defs>
            <path d="M200,400 C89.54305,400 0,310.45695 0,200 C0,89.54305 89.54305,0 200,0 C310.45695,0 400,89.54305 400,200 C400,310.45695 310.45695,400 200,400 Z M200,360 C288.36556,360 360,288.36556 360,200 C360,111.63444 288.36556,40 200,40 C111.63444,40 40,111.63444 40,200 C40,288.36556 111.63444,360 200,360 Z" id="Combined-Shape" fill="url(#linearGradient-1)"></path>
            <text id="" font-size="200" font-weight="400" fill="#FFFFFF">
              <tspan x="100" y="272"></tspan>
            </text>
          </svg>
        </button>
        <button class="rating button" @click="score=fullScore">
          <svg width="25px" height="25px" viewBox="0 0 400 400" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
            <g id="Group-Copy">
              <path d="M200,400 C89.54305,400 0,310.45695 0,200 C0,89.54305 89.54305,0 200,0 C310.45695,0 400,89.54305 400,200 C400,310.45695 310.45695,400 200,400 Z M200,360 C288.36556,360 360,288.36556 360,200 C360,111.63444 288.36556,40 200,40 C111.63444,40 40,111.63444 40,200 C40,288.36556 111.63444,360 200,360 Z" id="Combined-Shape" fill="#00B5AD"></path>
              <text id="" font-size="200" font-weight="400" fill="#FFFFFF">
                <tspan x="100" y="272"></tspan>
              </text>
            </g>
          </svg>
        </button>
        </span>
        <span class="ui input">
          <input ref="input" type="number" name="" placeholder="" v-model.number="score" @keyup.enter="submitReview">
        </span>
        <button class="ui primary button" @click="submitReview" :class="typeof score === 'number' && score >= 0?'':'disabled'"></button>
      </div>
      <div class="display-remark" v-if="!reviewMode">
        <span>{{note || ''}}</span>
      </div>
      <div class="display-score" v-if="!reviewMode">
        <span>{{score}} / {{fullScore}}</span>
      </div>
    </div>
  </div>
</template>

<script>
/*
mutation
mutation 
*/
import swal from 'sweetalert'
import Artboard from 'assets/js/artboard'
import axios from 'axios'
import { CM_RECORD_STATUS_SUBMITTED } from 'assets/js/consts/common'
import { mapState, mapMutations } from 'vuex'
import 'components/icons/refresh'
import 'components/icons/undo'
import 'components/icons/slide-show'
import 'components/icons/redo'
import 'components/icons/close'
import 'components/icons/caret-down'
import 'components/icons/rotate-right'
import Icon from 'components/icons/Icon'

const qs = require('qs')
const $script = require('scriptjs')

export default {
  name: 'sketch-pad',
  props: {
    studentName: { type: String, required: true },
    question: { type: Object, required: true },
    result: { type: Object, required: true },
    canClose: { type: Boolean, required: false },
    fixedTop: { type: Boolean, required: false },
  },
  data() {
    const reviewMode = this.result.status === CM_RECORD_STATUS_SUBMITTED
    return {
      artboards: [],
      artboard: null,
      score: reviewMode ? null : this.result.score,
      note: this.result.note || '',
      reviewMode,
      viewer: null,
    }
  },
  components: { Icon },
  mounted() {
    const canvasWrapper = this.$el.querySelector('.canvas-wrapper')
    canvasWrapper.style.height = `${Math.round(canvasWrapper.offsetWidth * 1200 / 1920 - 70)}px`
    const r = this.$el.querySelector('.artboard-wrapper')
    if (this.fixedTop) {
      $('html, body').animate({
        scrollTop: $(this.$el).offset().top,
      })
    }

    for (const img of this.result.images) {
      const artboard = new Artboard({
        imageSrc: img.url,
        boundingClientWidth: r.offsetWidth,
        boundingClientHeight: r.offsetHeight,
      })
      artboard.appendTo(r)
      artboard.imageId = img.id
      this.artboards.push(artboard)
    }
    if (this.artboards.length > 0) {
      this.artboard = this.artboards[0]
    }
    if (this.reviewMode) {
      this.startReviewMode()
    }
  },
  computed: {
    statusLabel() {
      if (this.result.status === CM_RECORD_STATUS_SUBMITTED) return ''
      return ''
    },
    fullScore() {
      return this.question.score
    },
    recordId() {
      return this.result.recordId
    },
    status() {
      return this.result.status
    },
    canUndo() {
      if (this.artboard && this.artboard.canUndo) {
        return true
      }
      return false
    },
    canRedo() {
      if (this.artboard && this.artboard.canRedo) {
        return true
      }
      return false
    },
    canClear() {
      if (this.artboard && this.artboard.canClear) {
        return true
      }
      return false
    },
    hasSucceedListener() {
      return this.$listeners && this.$listeners.succeed
    },
    ...mapState(['cm']),
  },
  beforeDestroy() {
    if (this.viewer) {
      this.viewer.destroy()
    }
  },
  watch: {
    artboard(val, oldVal) {
      if (oldVal) oldVal.hide()
      if (val) val.show()
    },
    score(val) {
      if (val < 0) this.score = 0
      if (val > this.fullScore) this.score = this.fullScore
    },
    'question.results': function (results) {
      for (const r of results) {
        if (r.sid === this.result.sid) {
          this.$emit('update:result', r)
        }
      }
    },
    result(val) {
      for (const image of val.images) {
        for (const artboard of this.artboards) {
          if (image.id === artboard.imageId) {
            artboard.loadImageAsBase(image.url)
            this.artboard.clear()
          }
        }
      }
    },
  },
  methods: {
    openViewer() {
      if (!this.viewer) {
        $script('/static/w/assets/viewer/viewer.js', () => {
          this.viewer = new Viewer(this.$el.querySelector('.mini-gallery'), {
            inline: false,
            button: true,
            navbar: true,
            title: false,
            scalable: false,
            keyboard: false,
            toolbar: {
              zoomIn: 4,
              zoomOut: 4,
              reset: 1,
              prev: 0,
              play: 0,
              next: 0,
              rotateLeft: 1,
              rotateRight: 1,
              flipHorizontal: 0,
              flipVertical: 0,
            },
            playControl: false,
            transition: false,
          })
          this.viewer.show()
        })
      } else {
        this.viewer.update()
        this.viewer.show()
      }
    },
    startReviewMode() {
      this.reviewMode = true
      this.$nextTick(() => {
        this.initLineStyle()
        for (const artboard of this.artboards) {
          artboard.activate()
          this.$el.querySelector('.artboard-wrapper').classList.add('drawing')
        }
        if (this.fixedTop) {
          this.$refs.input.focus()
          this.$refs.input.select()
        }
      })
    },
    endReviewMode() {
      this.reviewMode = false
      this.$nextTick(() => {
        for (const artboard of this.artboards) {
          this.$el.querySelector('.artboard-wrapper').classList.remove('drawing')
          artboard.deactivate()
        }
      })
    },
    initLineStyle() {
      const defaultStyle = {
        strokeWidth: 5,
        strokeOpacity: 0.7,
        stroke: '#F9CE63',
      }
      const localVal = localStorage.getItem(`${document.cookie}-pathStyle`)
      const localStyle = localVal && JSON.parse(localVal) || {}
      const pathStyle = Object.assign(defaultStyle, localStyle)

      const lineSpan = document.createElement('span')
      lineSpan.style.display = 'block'
      lineSpan.style.width = '40px'
      lineSpan.style.backgroundColor = pathStyle.stroke
      lineSpan.style.height = `${pathStyle.strokeWidth}px`
      lineSpan.style.opacity = pathStyle.strokeOpacity
      this.$el.querySelector('.init-line-style').innerHTML = ''
      this.$el.querySelector('.init-line-style').appendChild(lineSpan)
      $(this.$el.querySelector('.ui.dropdown.line-style-selection')).dropdown()
    },
    rotate() {
      if (this.canUndo || this.canRedo || this.canClear) {
        swal({
          text: '?',
          buttons: ['', ''],
        }).then((value) => {
          if (value) {
            this.clear()
            this.artboard.rotateImage()
          }
        })
      } else {
        this.artboard.rotateImage()
      }
    },
    undo() {
      if (this.artboard && this.artboard.canUndo) {
        this.artboard.undo()
      }
    },
    redo() {
      if (this.artboard && this.artboard.canRedo) {
        this.artboard.redo()
      }
    },
    clear() {
      if (this.artboard && this.artboard.canClear) {
        this.artboard.clear()
      }
    },
    changePenStyle(strokeWidth, color, opacity = 1) {
      const colorPattern = {
        red: '#F44336',
        pink: '#FA608B',
        green: '#7DC76C',
        yellow: '#F9CE63',
      }
      const pathStyle = { stroke: colorPattern[color], strokeWidth, strokeOpacity: opacity }
      localStorage.setItem(`${document.cookie}-pathStyle`, JSON.stringify(pathStyle))

      for (const artboard of this.artboards) {
        artboard.updatePathStyle(pathStyle)
      }
    },
    submitReview() {
      if (this.score === null || this.score < 0 || this.score > this.fullScore) {
        this.$toasted.error('', { duration: 700, position: 'bottom-right' })
        return
      }
      const initStatus = this.result.status
      const subjectiveReviewUrl = '/pad/j/review/question/subjective'
      const imagePostArr = []
      let imagePostCount = 0
      for (const a of this.artboards) {
        if (a.beenEdited) {
          imagePostCount++
          a.getSvgtoPngDataURL((pngData) => {
            const formData = new FormData()
            formData.append('forePngData', pngData)
            formData.append('imageId', a.imageId)
            formData.append('action', 'replace-image')
            formData.append('recordId', this.recordId)
            formData.append('questionKind', this.question.kind)
            formData.append('rotateCount', a.rotateCount)
            imagePostArr.push(axios.post(subjectiveReviewUrl, formData))
          })
        }
      }
      const params = {
        recordId: this.recordId,
        note: this.note,
        reviewScore: this.score,
        questionKind: this.question.kind,
      }
      const timerId = setInterval(() => {
        // setInterval  getSvgtoPngDataURL 
        if (imagePostArr.length < imagePostCount) {
          return
        }
        clearInterval(timerId)
        if (imagePostArr.length) {
          swal({ text: '...', button: false, closeOnClickOutside: false })
        }
        axios.all(imagePostArr)
          .then(axios.spread(() => {
            axios.post(subjectiveReviewUrl, qs.stringify(params)).then(() => {
              if (imagePostArr.length) {
                swal.close()
              }
              this.updateCmData()
              if (initStatus === CM_RECORD_STATUS_SUBMITTED && this.hasSucceedListener) {
                this.$emit('succeed')
              } else {
                this.$toasted.success('', { duration: 700, position: 'bottom-right' })
                this.endReviewMode()
              }
            })
          }))
      }, 1000)
    },
    updateCmData() {
      return axios.get('/pad/j/course-material/detail', { params: { cmId: this.cm.id } }).then((response) => {
        this.$store.commit('updateCm', response.data.cm)
      })
    },
    ...mapMutations(['updateCm']),
  },
}
</script>

<style>
  @import "/static/w/assets/viewer/viewer.css";
</style>

<style lang="less">
.viewer-container {
  background-color: rgba(0, 0, 0, .8);
}
</style>

<style lang="less" scoped>
.note {
  color: #999;
  font-size: .8em;
}
.sketchpad {
  width: 100%;
  background-color: #323639;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
  font-size: 12px;
  color: white;
  border-radius: 4px;
}
.sketchpad-header {
  display: flex;
  .sketch-title {
    max-width: 22rem;
    width: 22rem;
  }
  .mini-gallery {
    flex: 1;
    text-align: center
  }
  .sketch-actions {
    text-align: right;
    max-width: 22rem;
    width: 22rem;
  }
}
.sketchpad-footer {
  display: flex;
  .remark {
    flex: 1;
    .ui.input {
      width: 100%;
      input {
        width: 100%;
        min-width: 18rem;
      }
    }
  }
  .rating {
    max-width: 25rem;
    .buttons {
      display: inline-flex;
      vertical-align: middle;
    }
    .button.rating {
      background-color: transparent;
      border: transparent;
      padding: 0;
      margin: 0 5px;
      cursor: pointer;
      outline: none;
    }
  }
  .display-remark {
    flex: 1;
  }
  .display-score {
    max-width: 15rem;
    text-align: right;
  }
}
.sketch-title, .sketch-actions {
  font-size: 15px;
}
.sketch-title, .sketch-actions, .rating, .remark, .display-remark, .display-score {
  padding: 5px 5px;
}

.sketch-actions {
  .pad.button {
    font-size: 15px;
    color: #fff;
    margin-right: 0;
    margin-left: 4px;
  }
  .pad.rotate.button {
    margin-right: 1.5em;
  }
}

.mini-gallery {
  height: 38px;
  img {
    max-width: 100px;
    max-height: 35px;
    height: 100%;
    width: auto;
    margin-right: .5em;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    vertical-align: middle;
  }
}

.canvas-wrapper {
  background-color: #525659;
  text-align: center;
  padding: 14px;
  .artboard-wrapper {
    width: 100%;
    height: 100%;
    &.drawing {
      cursor: url('~assets/img/pencil.cur') 0 0, auto;
    }
  }
}

.ui.input input {
  border: none;
  line-height: 1.2;
  background-color: #eee;
  &::placeholder {
    color: #999;
  }
}

.rating {
  align-self: end;
  .ui.input input {
    width: 65px;
  }
}

.ui.button, .ui.input input {
  font-size: 11px;
  padding: 6px 11px;
  line-height: 1.2;
}

.line-style {
  width: 40px;
  background-color: #F44336;
  display: block;
}

.line-thickness-1 {
  height: 1px;
}
.line-thickness-3 {
  height: 3px;
}
.line-thickness-5 {
  height: 5px;
}
.line-thickness-10 {
  height: 10px;
}
.line-thickness-15 {
  height: 15px;
}

.line-opacity-50 {
  &.line-color-pink {
    background-color: rgba(250, 96, 139, .7);
  }
  &.line-color-green {
    background-color: rgba(125, 199, 108, .7);
  }
  &.line-color-yellow {
    background-color: rgba(249, 206, 99, .7);
  }
}
.ui.dropdown.line-style-selection {
  border: #555 solid 1px;
  border-radius: 4px;
  padding: 0 8px;
}
.ui.dropdown .menu>.item {
  padding: 4px 8px !important;
  min-height: 14px;
  display: inline-block;
  width: 24%;
}

.ui.dropdown .menu {
  background-color: rgba(0, 0, 0, 0.7);
  min-width: 300px;
  padding: 8px;
  .header {
    color: white;
    margin: 8px 0;
    padding: 0 8px;
  }
}

</style>



